import React from "react";
import { Viewer, Entity, PointGraphics, CameraFlyTo } from "resium";
import { Cartesian3, Color } from "cesium";

export default class MyScene extends React.Component {
    constructor() {
        super();
        this.state = {}
    }

    componentDidMount() {
        this.timer = setInterval(() => {
            this.getExportData();
        }, 1000);

        this.getExportData();
    }

    componentWillUnmount() {
        if (this.timer) {
            window.clearInterval(this.timer);
        }
    }

    getExportData() {
        const requestOptions = {
            method: 'GET',
            headers: { 'Content-Type': 'application/json' },
        };
        fetch('https://augmentedrealitywhiteboardbackend.azurewebsites.net/exportdata', requestOptions)
            .then(response => response.json())
            .then(data => {
                var needsFlyTo = true;
                if ((Math.abs((this.state.exportData?.anchor?.latitude ?? 0) - (data?.anchor?.latitude ?? 0)) < Number.EPSILON)
                    && (Math.abs((this.state.exportData?.anchor?.longitude ?? 0) - (data?.anchor?.longitude ?? 0)) < Number.EPSILON)) {
                    needsFlyTo = false;
                }
                console.log(this.state.exportData?.anchor)
                console.log(needsFlyTo);
                this.setState({ needsFlyTo: needsFlyTo, exportData: data })
            });
    }

    render() {
        return (
            <Viewer full>
                {this.state?.exportData?.anchor ? (<Entity
                    position={Cartesian3.fromDegrees(
                        this.state.exportData.anchor.longitude,
                        this.state.exportData.anchor.latitude
                    )}
                    name={"Anchor"}
                    description={"Home"}
                >
                    <PointGraphics pixelSize={10} color={Color.RED} />
                </Entity>) : undefined}

                {this.state?.exportData?.markers ? this.state.exportData.markers.map((d, i) => (
                    <Entity
                        key={i}
                        position={Cartesian3.fromDegrees(d.longitude, d.latitude)}
                        name={d.name}
                        description={d.type}
                    >
                        <PointGraphics pixelSize={10} color={Color.BLUE} />
                    </Entity>
                )) : undefined}
                {this.state?.needsFlyTo ? (<CameraFlyTo
                    destination={Cartesian3.fromDegrees(
                        this.state.exportData?.anchor?.longitude ?? 0,
                        this.state.exportData?.anchor?.latitude ?? 0,
                        1000000
                    )}
                    duration={3}
                />) : undefined}
            </Viewer>
        );
    }
}